@keyframes indeterminate { 0% { left: -35%;
    right: 100%; }
  60% { left: 100%;
    right: -90%; }
  100% { left: 100%;
    right: -90%; } }

@keyframes indeterminate-short { 0% { left: -200%;
    right: 100%; }
  60% { left: 107%;
    right: -8%; }
  100% { left: 107%;
    right: -8%; } }

.js-loading-mask { position: fixed 0; z-index: 9999; overflow: hidden; width: 100%; height: 100%; background-color: #FFF; }

.js-loading-mask .progress { position: absolute; top: 50%; left: 50%; height: 4px; display: block; width: 20%; background-color: #FFEBEE; border-radius: 2px; margin: .5rem 0 1rem 0; overflow: hidden; transform: translate(-50%, -50%); }

.js-loading-mask .progress .indeterminate { background-color: #cd0505; }

.js-loading-mask .progress .indeterminate::before { content: ''; position: absolute; background-color: inherit; top: 0; left: 0; bottom: 0; will-change: left, right; animation: indeterminate 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite; }

.js-loading-mask .progress .indeterminate::after { content: ''; position: absolute; background-color: inherit; top: 0; left: 0; bottom: 0; will-change: left, right; animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite; animation-delay: 1.15s; }

[type="radio"]:not(:checked), [type="radio"]:checked { position: absolute; opacity: 0; pointer-events: none; }

[type="radio"]:not(:checked) + label, [type="radio"]:checked + label { position: relative; padding-left: 35px; cursor: pointer; display: inline-block; height: 25px; line-height: 25px; font-size: 1rem; transition: .28s ease; user-select: none; }

[type="radio"]:not(:checked) + label:before, [type="radio"]:not(:checked) + label:after, [type="radio"]:checked + label:before, [type="radio"]:checked + label:after { border-radius: 50%; }

[type="radio"] + label:before, [type="radio"] + label:after { content: ''; position: absolute; left: 0; top: 0; margin: 4px; width: 16px; height: 16px; z-index: 0; transition: .28s ease; }

[type="radio"]:not(:checked) + label:before, [type="radio"]:not(:checked) + label:after { border: 1px solid rgba(0, 0, 0, 0.15); }

[type="radio"]:not(:checked) + label:after { transform: scale(0); }

[type="radio"]:checked + label:before { border: 2px solid #2196F3; }

[type="radio"]:checked + label:after { background-color: #2196F3; border: 1px solid #2196F3; transform: scale(0.5); }

.mdl-layout__container { position: relative; }

.col { overflow-x: hidden; }

a, a:focus, button, button:focus, [role="button"], [role="button"]:focus { outline: none; }

.btn:focus { box-shadow: none; }

.mdl-layout__header { background: #2196F3; }

