// lost
%clearfix {
	lost-utility: clearfix; }





// 單行省略
@mixin text-ellipsis {
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis; }
%text-ellipsis {
	@include text-ellipsis; }

// 多行省略
@mixin text-more-ellipsis {
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical; }
%text-more-ellipsis {
	@include text-more-ellipsis; }

// 不斷行
@mixin nowrap {
	white-space: nowrap; }
%no-wrap {
	@include nowrap; }





// 超連結
@mixin hover {
	&:hover, &:focus {
		@content; } }





// 圖片
@mixin img-responsive {
	display: block;
	max-width: 100%;
	height: auto; }
%img-responsive {
	@include img-responsive; }





// 陰影
@mixin box-shadow-z1 {
	box-shadow: $z1; }
%box-shadow-z1 {
	@include box-shadow-z1; }

@mixin box-shadow-z2 {
	box-shadow: $z2; }
%box-shadow-z2 {
	@include box-shadow-z2; }

@mixin box-shadow-z3 {
	box-shadow: $z3; }
%box-shadow-z3 {
	@include box-shadow-z3; }

@mixin box-shadow-z4 {
	box-shadow: $z4; }
%box-shadow-z4 {
	@include box-shadow-z4; }
